import axios from 'axios';
import styles from 'css/admin/admin.booklist.module.scss';
import { useEffect, useRef } from 'react';
import { useState } from 'react';
import Modal from './modal';
import styles2 from 'css/admin/admin.bookUpdate.module.scss';
import * as gvar from 'global_variables'



const MenuList=()=>{
    let imgurl = gvar.BACK_BASE_URL+'/images/';
    let url =gvar.BACK_BASE_URL+'/menuList';
    let [data,setData] = useState([]);
    let [selectedData,setSelectedData] = useState([]);
    let [searchInput,setSearchInput] = useState();
    let [searchData,setSearchData] = useState([]);
    let [visible, setVisible] = useState(false);

    
    const deleteBook = (id,index)=>{
        if(window.confirm("정말 삭제하시겠습니까?")){
            axios.post('http://localhost:8080/bookDelete',null,{
                params:{
                    id:123
                }
            })
                .then((res)=>{
                    let arr = [...data];
                    arr.splice(index,1);
                console.log(arr);
                setData(arr);
            }).catch()
        }
    }
    const getList = ()=>{
        axios.get(url).then((res)=>{
            setData([...res.data]);
        }).catch()
    }
    useEffect(()=>{
        getList();
    },[])
    useEffect(()=>{
        console.log(data);
        setSearchData([...data]);
    },[data])

    useEffect(()=>{
        console.log(searchInput);
        let arr = [...data];
        let result = arr.filter((value)=> value.title.indexOf(searchInput) >=0);
        console.log(result);
        setSearchData(result);

    },[searchInput])

    useEffect(()=>{
        console.log(visible);
    },[visible])
    
    
    const closeModal = ()=>{
        setVisible(false);
        console.log("모달실행됨");
    }

    const selectData = (value)=>{
        setVisible(true);
        setSelectedData(value);
    }

    return(
        <div className={styles.wrap}>
            <div className={styles.searchBox}>
            <select>
                <option key="banana" value="banana">제목</option>
            </select>
            </div>
                <label>빠른검색</label>
                <input onChange={(e)=>{setSearchInput(e.target.value)}} value={searchInput}></input>
            <table>
                <thead>
                    <tr>
                        <th>번호</th>
                        <th style={{width:'100px'}}>이미지</th>
                        <th>이름</th>
                        <th>가격</th>
                        <th>재고</th>
                        <th>카테고리</th>
                        <th style={{width:'100px'}}>비고</th>
                    </tr>
                </thead>
                <tbody>
                    {searchData.map((value,index) => {
                        return (
                            <tr key={value.id}>
                                <td>{index+1}</td>
                                <td><img src={imgurl+value.photoPath}></img></td>
                                <td>{value.name}</td>
                                <td>{value.price}</td>
                                <td>{value.stockQuantity}</td>
                                <td>{value.category}</td>
                                <td><button onClick={()=>{selectData(value)}} style={{marginBottom:'5px'}}>수정</button>
                                <button onClick={()=>{deleteBook(value.id,index)}}>삭제</button>
                                </td>
                            </tr>
                        )
                    })}

                </tbody>
            </table>
            {visible?
            <MenuUpdateModal selectedData={selectedData} visible={visible} closeModal={closeModal}></MenuUpdateModal>
            :null
            }
            
        </div>
    )
}

const MenuUpdateModal = ({visible, closeModal, selectedData})=>{
    let 메뉴이름ref = useRef();
    let 카테고리ref = useRef();
    let 가격ref = useRef();
    let 재고ref = useRef();
    let 이미지경로ref = useRef();
    let [이미지,set이미지] = useState('/img/cartoon/naruto.jpg');
    const SaveDB = ()=>{

    }
    useEffect(()=>{
        cencel();
    },[selectedData])

    const cencel =()=>{
        카테고리ref.current.value=selectedData.category;
        메뉴이름ref.current.value=selectedData.name;
        가격ref.current.value=selectedData.price;
        재고ref.current.value=selectedData.stockQuantity;
        이미지경로ref.current.value=selectedData.photoPath;
    }
    if(visible){
        return(
            <Modal closeModal={closeModal}>
                  <div className={styles2.tbdiv}>
                    <table>
                        <tbody>
                            <tr>
                                <td>
                                    <label>카테고리</label>
                                </td>
                                <td>
                                    <input ref={카테고리ref}></input>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label>이름</label>
                                </td>
                                <td>
                                    <input ref={메뉴이름ref}></input>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label>가격</label>
                                </td>
                                <td>
                                    <input ref={가격ref}></input>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label>재고</label>
                                </td>
                                <td>
                                    <input ref={재고ref}></input>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label>이미지</label>
                                    
                                </td>
                                <td>
                                    <input readOnly ref={이미지경로ref} style={{width:'300px'}}></input>
                                    <button>찾기</button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div className={styles2.btns}>
                        <button onClick={cencel}>취소</button>
                        <button onClick={SaveDB}>수정하기</button>
                    </div>
    
                </div>
                <div className={styles2.imgdiv}>
                    <h4>이미지 미리보기</h4><br></br>
                    <img src={이미지?이미지:null}></img>
                </div>
            </Modal>
        )
    }
}
export default MenuList;